:root {
  --split-ui-color-light: #eef0f2;
  --split-ui-color-dark: #20252d;
  --split-ui-support-color: #00bfaa;
  --center-box-border-radius: 0.8rem;

  --red-color: #dc3545;
  --blue-color: #007bff;
  --tan-color: #d5c2a9;
  --black-color: #212529;
  --white-color: #fff;
  --green-color: #00bfaa;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  color: aquamarine;
  overflow: hidden;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: var(--white-color);
  border: 3px solid var(--black-color);
  border-radius: 8px;
  float: left;
  font-weight: bold;
  font-size: 18px;
  line-height: 60px;
  height: 60px;
  width: 60px;
  margin-right: 6px;
  margin-top: 6px;
  padding: 0;
  text-align: center;
  color: var(--green-color);
}

.selected-square {
  background: var(--green-color);
  border: 3px solid var(--black-color);
  border-radius: 8px;
  float: left;
  font-weight: bold;
  font-size: 18px;
  line-height: 60px;
  height: 60px;
  width: 60px;
  margin-right: 1px;
  margin-top: 1px;
  padding: 0;
  text-align: center;
  color: var(--white-color);
  transition: ease-out 0.5s;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  /* display: flex; */
  /* flex-direction: row; */
  background-color: var(--black-color);
  padding: 10px;
  
}

.game-board {
  padding: 10px;
}

.event-image {
  height: auto;
  width: 600px;
  border-radius: 10px;
}

.page-heading {
  font-size: 30px;
  margin-left: 20px;
  color: aquamarine
}

.accessories-column {
  margin-top: 20px;
  padding-left: 90px;
}

.text-box {
  width: 600px;
  height: 50px;
  margin-top: 15px;
  border-radius: 1rem;
  border-left: 1rem solid var(--green-color);
  padding: 10px;
  background: hsla(0,0%,100%,.2)!important;
  font-size: 18px;
}

.next-arrow {
  width: 20px;
  height: 20px;
  margin-top: -50px;
  margin-left: 500px;
  color: aliceblue;
  cursor: pointer;
}

.footnote {
  text-align: center;
  font-size: 16px;
  margin-bottom: 300px;
  margin-top: 80px;
}

.text-transition {
  transition: ease-out 1s;
}

.heart {
  color: #ff0000;
}

.current-name-text {
  animation-timing-function: steps(5);

}

.typewriter {
  font-family: Courier, monospace;
display: inline-block;
}

.typewriter-text {
  display: inline-block;
  overflow: hidden;
  letter-spacing: 2px;
  animation: typing 2s steps(30, end), blink .75s step-end infinite;
  white-space: nowrap;
  font-size: 30px;
  font-weight: 700;
  box-sizing: border-box;
}

@keyframes typing {
  from { 
      width: 0% 
  }
  to { 
      width: 100% 
  }
}

@keyframes blink {
  from, to { 
      border-color: transparent 
  }
  50% { 
      border-color: orange; 
  }
}

.number-input {
  background-color: transparent;
  border: transparent;
  border-left: transparent;
  border-right: transparent;
  width: 60px;
  color: aquamarine;
}
 .number-input:focus {
   border-color: #dc3545;
 }

 .verify-button {
   margin-left: 150px;
   background-color: transparent;
   color: aquamarine;
   border: transparent;
 }

 .wrong-symbol {
   transform: rotateZ(45deg);
   color: #dc143c;
 }

 .right-symbol {
   color: rgb(8, 255, 8);
 }

 #verify-button, #next-number-input{
   outline: none
 }
